<template>
  <div id="Turntable">
    <div class="ly-plate">

      <div class="rotate-bg"></div>

      <div class="lottery-star"><img
          src="../../static/images/router/rotate-static.png"
          id="lotteryBtn"
        ></div>

    </div>
    <home-footer></home-footer>
  </div>
</template>

<script>
import homeFooter from "../components/Footer";
import { Toast } from "mint-ui";
import "../../static/js/jquery.min.js";
import "../../static/js/jQueryRotate.2.2.js";
import "../../static/js/jquery.easing.min.js";
export default {
  name: "Turntable",
  data() {
    return {
      msg: "Welcome to Turntable"
    };
  },
  components: {
    homeFooter
  },
  mounted() {
    document.documentElement.style.height = "100%";
    document.body.style.height = "100%";
    document.querySelector("#app").style.height = "100%";
    var rotateFunc = function(awards, angle, text) {
      //awards:奖项，angle:奖项对应的角度

      $("#lotteryBtn").stopRotate();

      $("#lotteryBtn").rotate({
        angle: 0,

        duration: 5000,

        animateTo: angle + 1440, //angle是图片上各奖项对应的角度，1440是我要让指针旋转4圈。所以最后的结束的角度就是这样子^^

        callback: function() {
          Toast({
            message: text
          });
          localStorage.date = oDate.getDate();
        }
      });
    };

    let oDate = new Date();

    $("#lotteryBtn").rotate({
      bind: {
        click: function() {
          if (localStorage.date && oDate.getDate() == localStorage.date) {
            Toast({
              message: "您今天次数已用完，请明日再来哦"
            });
            return false;
          }
          var time = [1];

          time = time[Math.floor(Math.random() * time.length)];

          if (time == 0) {
            //timeOut(); //网络超时
          }

          if (time == 1) {
            var data = [1, 2, 3, 0]; //返回的数组

            data = data[Math.floor(Math.random() * data.length)];

            if (data == 1) {
              rotateFunc(1, 157, "恭喜您抽中的一等奖");
            }

            if (data == 2) {
              rotateFunc(2, 247, "恭喜您抽中的二等奖");
            }

            if (data == 3) {
              rotateFunc(3, 22, "恭喜您抽中的三等奖");
            }

            if (data == 0) {
              var angle = [67, 112, 202, 292, 337];

              angle = angle[Math.floor(Math.random() * angle.length)];

              rotateFunc(0, angle, "很遗憾，这次您未抽中奖");
            }
          }
        }
      }
    });
  },
  methods: {
    jumpInfo() {
      this.$router.push({ name: "Info", params: {} });
    },
    jumpPhoto() {
      this.$router.push({ name: "Photo", params: {} });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  padding: 0;
  margin: 0;
}

#Turntable {
  padding: 0.5rem 0;
  background: url("../../static/images/router/timg.jpg") no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: calc(100% - 1rem);
}
.ly-plate {
  position: relative;

  width: 3rem;

  height: 3rem;

  margin: 0 auto;
}

.rotate-bg {
  width: 3rem;

  height: 3rem;

  background: url("../../static/images/router/ly-plate.png");
  background-size: 100%;

  position: absolute;

  top: 0;

  left: 0;

  margin-top: 0%;
}

.ly-plate div.lottery-star {
  width: 3rem;

  height: 3rem;

  position: absolute;

  top: 0;

  left: 0;

  /*text-indent:-999em;

	overflow:hidden;

	background:url(rotate-static.png);

	-webkit-transform:rotate(0deg);*/

  outline: none;
}

.ly-plate div.lottery-star #lotteryBtn {
  cursor: pointer;

  position: absolute;

  top: 50%;

  left: 50%;

  width: 1.8rem;
  height: 1.8rem;
  margin-left: -0.9rem;
  margin-top: -0.9rem;
}
</style>



